import React, { ReactNode, Suspense, useRef, useState } from "react"

// import react-three-fiber
import { Canvas, extend, useThree } from "react-three-fiber"

import { TexturedPlane } from "./text";

//import Stats from 'three/addons/libs/stats.module.js';
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';
extend({OrbitControls});

// import { Scene } from "../common/three/scene";

export const CameraControls = (props: any) => {
  const initialArgs: any[] = [];
  const [controlArgs, setControlArgs] = useState(initialArgs);

  useThree(function({camera, gl}) {
    if(!controlArgs.length) {
      console.log('Setting Camera control args: ', camera, gl.domElement);
      setControlArgs([camera, gl.domElement]);
    }
    // camera.rotation.set(deg2rad(30), 0, 0);
  });

  return (<>{
    controlArgs.length == 2 &&
    //@ts-ignore
    <orbitControls args={controlArgs} target={[0, 0, 0]}/>}
    </>
  );
};

interface OrbitCanvasProps {
  children?: ReactNode;
  scene: string;
};

export const OrbitCanvas = ({children, scene = ''}: OrbitCanvasProps) => {
  const initialCanvasData = {
    ready: false,
    camera: null,
    gl: null,
  };
  const cameraStartPosition = [0, 2, 10];
  const [canvasData, setCanvasData] = useState(initialCanvasData);
  const canvas: any = useRef();

  const canvasOnCreated = ({gl, camera}: any) => {
    console.log('canvasOnCreated', canvas, camera, gl)
    if( 2%2 ) {
      gl.setClearColor("#335")
    }
    setCanvasData({
      ready: true,
      camera: camera,
      gl: gl,
    });
  };

  const onDoubleClick = () => {
    const camera: any = canvasData.camera;
    camera.position.set(...cameraStartPosition);
    camera.lookAt(0, 0, 0);
  };

  return <div className={`three-canvas-wrapper`}>
    <Suspense fallback={`Render canvas...`}>
      <Canvas camera={{ position: cameraStartPosition }} ref={canvas} onCreated={canvasOnCreated} onDoubleClick={onDoubleClick}>
        {canvasData && canvasData.ready && <>
          <CameraControls />
          {getScene(scene)}
          {children}
        </>}
      </Canvas>
    </Suspense>
    </div>
}

interface SceneProps {
  size?: number;
  elevation?: number;
  ambientLightIntensity?: number;
  pointLightPositionY?: number;
}

const LiquidGold4KScene = (props: SceneProps) => (<>
  <TexturedPlane size={props.size || 4096} elevation={props.elevation || -1} />
  <ambientLight intensity={props.ambientLightIntensity || 0.2} />
  <pointLight position={[10, props.pointLightPositionY || 50, 10]} />
</>);

function getScene(scene: string) {
  scene = (scene || '').replace(/\W+/g, '');

  switch(scene) {
    case 'gold':
    case 'liquidgold':
      return <LiquidGold4KScene size={1000} />;
    case 'gold4k':
    case 'liquidgold4k':
      return <LiquidGold4KScene />;
    default:
      return <LiquidGold4KScene />;
  }
}
